import axios from "axios";
import { url, secretkey } from "../url";
import { decryptId, decryptUserId, decryptToken, logout } from "../Components/Admin/utils";
import $ from "jquery";
import CryptoJS from "crypto-js";

export const isLogin = () => dispatch => {

  if(!decryptUserId() || !decryptToken() || !decryptId()){
    dispatch({
      type: "IS_LOGIN",
      payload: false
    })
  }

  axios
  .get(`${url}GetUser?Userid=${parseInt(decryptUserId())}`)
  .then(res => {
    if(decryptToken()===`${res.data[0].emailAddr}${res.data[0].Pwd}` && parseInt(decryptId()) === parseInt(res.data[0].CompId) && parseInt(decryptUserId()) === parseInt(res.data[0].userid)){
      dispatch({
        type: "IS_LOGIN",
        payload: true
      });
    } else {
      logout()
    }
  })
  .catch(err =>
    dispatch({
      type: "IS_LOGIN",
      payload: 404
    })
  );
}

export const login = (values, history) => dispatch => {
  axios
    .get(`${url}Login?EmailAddress=${values.email}&UserPwd=${values.password}`)
    .then(res => {
      if(res.data[0].UserType === "A"){
        let ciphertext = CryptoJS.AES.encrypt(
          `${res.data[0].emailAddr}${res.data[0].Pwd}`,
          secretkey
        );
        let userid = CryptoJS.AES.encrypt(
          `${res.data[0].userid}`,
          secretkey
        );
        let CompId = CryptoJS.AES.encrypt(
          `${res.data[0].CompId}`,
          secretkey
        );
        localStorage.setItem("atkn", ciphertext.toString());
        localStorage.setItem("auid", userid.toString());
        localStorage.setItem("acid", CompId.toString());
        history.push("/adashboard");
        console.log(res.data[0].userid)
        dispatch({
          type: "LOGIN",
          payload: res.data
        });
      } else {
        dispatch({
          type: "GET_ERRORS",
          payload: 404
        });
      }
    })
    .catch(res => {
      dispatch({
        type: "GET_ERRORS",
        payload: 404
      });
    });
};

export const addCourseDetails = (values, setState) => dispatch => {
  axios
    .post(
      `${url}AddCourseDetails?CourseId=${values.courseid}&VideoTitle=${
        values.videoTitle
      }&VideoLink=${values.videoLink}&VideoDuration=${parseInt(
        values.videoDuration
      )}:00&WeekId=${parseInt(values.videoWeek)}`
    )
    .then(res => {
      $("#addModal").modal("hide");
      setState({
        loadingAdd: false,
        videoTitle: "",
        videoLink: "",
        videoDuration: "",
        videoWeek: ""
      });
      dispatch({
        type: "ADD_COURSE_DETAILS",
        payload: false
      });
    })
    .catch(err => {
      dispatch({
        type: "ADD_COURSE_DETAILS",
        payload: {
          payload: false,
          status: 500
        }
      });
    });
};
