export const addCourseMain = (values, history) => dispatch => {
  axios
    .post(
      `http://198.37.118.15:7899/api/LearnDvl/AddCourseMain?InstructorId=${
        values.InstructorId
      }&CourseDescription=${values.CourseDescription}&CourseIntroduction=${
        values.CourseIntroduction
      }&VideosDuration=${values.VideosDuration}&batchDuration=${parseFloat(
        values.batchDuration
      )}&CourseFee=${values.CourseFee}&VideoMedium=${
        values.VideoMedium
      }&whatwillyoulearn=${values.whatwillyoulearn}&TopicId=1&CategoryId=${
        values.CategoryId
      }&CompanyId=${decryptId()}`
    )
    .then(res => {
      let courseid = res.data.slice(-1)[0].courseid;
      // postFile(courseid, instructorid, res.data, values.courseImage)

      var bodyFormData = new FormData();
      bodyFormData.append("", values.courseImage);
      axios
        .post(
          `${url}PostFile?CompanyId=${decryptId()}&CourseId=${parseInt(
            courseid
          )}&InstructorId=0`,
          bodyFormData,
          {
            headers: {
              "Content-Type": `multipart/form-data`
            }
          }
        )
        .then(resFinal => {
          history.push("/adashboard");
          dispatch({
            type: "ADD_COURSE_MAIN",
            payload: res.data
          });
        })
        .catch(err =>
          dispatch({
            type: "ADD_COURSE_MAIN",
            payload: 500
          })
        );
    })
    .catch(err =>
      dispatch({
        type: "ADD_COURSE_MAIN",
        payload: 400
      })
    );
};

