export const updateUser = (values, history) => dispatch => {
  const monthNames = [
    "January",
    "February",
    "March",
    "April",
    "May",
    "June",
    "July",
    "August",
    "September",
    "October",
    "November",
    "December"
  ];

  let temp = new Date(values.dob)
  let dateofbirth = `${temp.getDate()}-${
    monthNames[temp.getMonth()]
  }-${temp.getFullYear()}`;

  console.log(values.emailAddr)
  axios
    .post(
      `${url}UpdateUser?userId=${values.userid}&firstname=${values.firstname}&middlename=${values.middlename}&lastname=${values.lastname}&EmailAddr=${values.emailAddr}&mailaddr=${values.mailingaddress}&countryid=${values.countryid}&cityid=${values.cityid}&qualificationid=${values.qualificationid}&futurequalificationid=${values.FutureQualificationId}&dob=${dateofbirth}&cnic=${values.cnic}&companyid=${values.CompId}&pwd=${values.Pwd}&gender=${values.gnder}&UserType=U`
    )
    .then(res => {
      window.location.reload()
      dispatch({
        type: "UPDATE_USER",
        payload: 200
      });
    })
    .catch(err => {
      window.location.reload()
      dispatch({
        type: "UPDATE_USER",
        payload: 500
      });
    });
};


