import React from "react";
import AdminDrawer from "./AdminDrawer";
import { withRouter } from "react-router-dom";
import {
  getAllUsersByCompany,
  deleteUser,
  updateUser,
  isLogin
} from "../../Actions/adminActions";
import {
  getAllCountries,
  getAllCityByCountry,
  getQualification,
  getAllCompanies,
} from "../../Actions/authActions";
import { connect } from "react-redux";
import "./AddCourse.css";
import Loader from "../../Loader";
import isEmpty from "../Protected/validation/is-empty";
import "./Courses.css";

// const monthNames = [
//   "JAN",
//   "FEB",
//   "MAR",
//   "APR",
//   "MAY",
//   "JUNE",
//   "JULY",
//   "AUG",
//   "SEPT",
//   "OCT",
//   "NOV",
//   "DEC"
// ];

class StudentsList extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      userid: "",
      firstname: "",
      middlename: "",
      lastname: "",
      dob: "",
      cnic: "",
      gnder: "",
      countryid: "",
      cityid: "",
      mailingaddress: "",
      qualificationid: "",
      Pwd: "",
      emailAddr: "",
      CompId: "",
      FutureQualificationId: "",
      UserStatus: "",
      errors: {},
      users: "",
      loadingDelete: false
    };
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.getallusersbycompany) {
      this.setState({
        users: nextProps.getallusersbycompany
      });
    }
  }

  componentDidMount() {
    this.props.getAllUsersByCompany();
    this.props.getAllCountries();
    this.props.getQualification();
    this.props.getAllCompanies();

    this.props.isLogin();

    if(!isEmpty(this.props.islogin)){
      if(!this.props.islogin){
        this.props.history.push('/admin')
      }
    }
  }

  handleImage = event => {
    if (event.target.files && event.target.files[0]) {
      this.setState({
        courseImage: URL.createObjectURL(event.target.files[0]),
        courseImageURL: event.target.files[0]
      });
    }
  };

  handleDate = event => {
    this.setState({ errors: {} });

    this.setState({
      dob: event.target.value
    });
    console.log(event.target.value);
  };

  handleInputChange = event => {
    this.setState({ errors: {} });

    this.setState({
      [event.target.name]: event.target.value
    });
  };

  onClick = e => {
    e.preventDefault();
    const errors = {};

    if (isEmpty(this.state.firstname)) {
      errors.firstname = "Firstname is Required";
    }
    if (isEmpty(this.state.middlename)) {
      errors.middlename = "Middlename is Required";
    }
    if (isEmpty(this.state.lastname)) {
      errors.lastname = "Lastname is Required";
    }
    if (isEmpty(this.state.dob)) {
      errors.dob = "Date of birth is Required";
    }
    if (isEmpty(this.state.cnic)) {
      errors.cnic = "Cnic is Required";
    }
    if (isEmpty(this.state.gnder)) {
      errors.gnder = "Gender is Required";
    }
    if (isEmpty(this.state.countryid)) {
      errors.countryid = "Country is Required";
    }
    if (isEmpty(this.state.cityid)) {
      errors.cityid = "City is Required";
    }
    if (isEmpty(this.state.mailingaddress)) {
      errors.mailingaddress = "Address is Required";
    }
    if (isEmpty(this.state.qualificationid)) {
      errors.qualificationid = "Qualification is Required";
    }
    if (isEmpty(this.state.Pwd)) {
      errors.Pwd = "Password field is Required";
    }
    if (isEmpty(this.state.emailAddr)) {
      errors.emailAddr = "Email Address is Required";
    }
    if (isEmpty(this.state.CompId)) {
      errors.CompId = "Select any Company";
    }
    if (isEmpty(this.state.UserStatus)) {
      errors.UserStatus = "User Status must be active or inactive";
    }

    this.setState({ errors });
    if (isEmpty(errors)) {
      this.setState({ loading: true, errors: {} });
      const values = {
        userid: this.state.userid,
        firstname: this.state.firstname,
        middlename: this.state.middlename,
        lastname: this.state.lastname,
        dob: this.state.dob,
        cnic: this.state.cnic,
        gnder: this.state.gnder,
        countryid: this.state.countryid,
        cityid: this.state.cityid,
        mailingaddress: this.state.mailingaddress,
        qualificationid: this.state.qualificationid,
        Pwd: this.state.Pwd,
        emailAddr: this.state.emailAddr,
        CompId: this.state.CompId,
        FutureQualificationId: this.state.FutureQualificationId,
        UserStatus: this.state.UserStatus
      };
      console.log(this.state.emailAddr)
      this.props.updateUser(values, this.props.history);
    }
  };

  myDate = date => {
    let newDate;
    newDate = new Date(date);
    let day = newDate.getDate();
    // let month = monthNames[newDate.getMonth()];
    let year = newDate.getFullYear();
    return `${year}-${
      newDate.getMonth() >= 1 && newDate.getMonth() <= 9
        ? `0${newDate.getMonth()}`
        : newDate.getMonth()
    }-${day >= 1 && day <= 9 ? `0${day}` : day}`;
  };

  getCities = id => {
    this.props.getAllCityByCountry(id);
  };

  deleteUser = () => {
    if (this.state.userid) {
      this.props.deleteUser(this.state.userid);
    } else {
      alert("Something went wrong!");
    }
  };

  render() {
    return isEmpty(this.props.getallusersbycompany) ||
      isEmpty(this.props.getallcountries) ||
      isEmpty(this.props.getallcompanies) ||
      isEmpty(this.props.getqualification) ||
      isEmpty(this.state.users) ? (
      <Loader />
    ) : (
      <AdminDrawer>
        {this.props.getallusersbycompany === 404 ||
        this.props.getinstructorbycompanyid === 404 ? (
          <div className="text-center">
            <h1>No students to show</h1>
          </div>
        ) : (
          <div className="container">
            <h2 className="text-center py-4">Students</h2>
            {this.state.users.map((user, index) => (
              <div className="card mb-4 cardShadow" key={index}>
                <h5 className="card-header headerIntro">
                  <span className="float-left">
                    {`${user.firstname} ${user.middlename} ${
                      user.lastname
                    }`.slice(0, 60)}
                  </span>
                  <span className="float-right">
                    {`Status: ${
                      user.UserStatus === "A" ? "Active" : "Inactive"
                    } / ${user.gnder === "M" ? "Male" : "Female"}`}
                  </span>
                </h5>
                <div className="card-body">
                  <h5 className="card-title">{`Email: ${user.emailAddr}`}</h5>
                  <p className="card-text">{`CNIC: ${user.cnic}`}</p>
                  <p className="card-text">{`Country: ${
                    this.props.getallcountries.find(
                      x => x.countryid === user.countryid
                    ).countryname
                  }`}</p>
                  <div
                    className="btn btn-outline-primary mr-2 buttonBorder"
                    data-toggle="modal"
                    data-target="#editModal"
                    onClick={() => {
                      this.setState({
                        userid: user.userid,
                        firstname: user.firstname,
                        middlename: user.middlename,
                        lastname: user.lastname,
                        dob: user.dob,
                        cnic: user.cnic,
                        gnder: user.gnder,
                        countryid: user.countryid,
                        cityid: user.cityid,
                        mailingaddress: user.mailingaddress,
                        qualificationid: user.qualificationid,
                        Pwd: user.Pwd,
                        emailAddr: user.emailAddr,
                        CompId: user.CompId,
                        FutureQualificationId: user.FutureQualificationId,
                        UserStatus: user.UserStatus
                      });
                      this.props.getAllCityByCountry(user.countryid);
                    }}
                  >
                    Edit
                  </div>
                  <div
                    className="btn btn-outline-danger"
                    data-toggle="modal"
                    data-target="#deleteModal"
                    onClick={() => this.setState({ userid: user.userid })}
                  >
                    Delete
                  </div>
                </div>
              </div>
            ))}
            {/* Edit Modal Start */}
            <div
              className="modal fade"
              id="editModal"
              tabIndex="-1"
              role="dialog"
              aria-labelledby="editModalLabel"
              aria-hidden="true"
            >
              <div
                className="modal-dialog modal-dialog-scrollable"
                role="document"
              >
                <div className="modal-content">
                  <div className="modal-header">
                    <h5 className="modal-title" id="editModalLabel">
                      Edit Course
                    </h5>
                    <button
                      type="button"
                      className="close"
                      data-dismiss="modal"
                      aria-label="Close"
                    >
                      <span aria-hidden="true">&times;</span>
                    </button>
                  </div>
                  <div className="modal-body">
                    {/* Edit Modal Body */}

                    <div>
                      <label className="labels pt-4">
                        <span className="imp">*&nbsp;</span>Firstname
                      </label>
                      <div className="input-group">
                        <input
                          type="text"
                          name="firstname"
                          value={this.state.firstname}
                          className="form-control"
                          onChange={this.handleInputChange}
                          maxLength={80}
                          placeholder="Enter your firstname"
                        />
                      </div>
                    </div>
                    {this.state.errors.firstname && (
                      <div className="imp textSize">
                        {this.state.errors.firstname}
                      </div>
                    )}

                    <div>
                      <label className="labels pt-4">
                        <span className="imp">*&nbsp;</span>Middlename
                      </label>
                      <div className="input-group">
                        <input
                          type="text"
                          name="middlename"
                          value={this.state.middlename}
                          className="form-control"
                          onChange={this.handleInputChange}
                          maxLength={80}
                          placeholder="Enter your middlename"
                        />
                      </div>
                    </div>
                    {this.state.errors.middlename && (
                      <div className="imp textSize">
                        {this.state.errors.middlename}
                      </div>
                    )}

                    <div>
                      <label className="labels pt-4">
                        <span className="imp">*&nbsp;</span>Lastname
                      </label>
                      <div className="input-group">
                        <input
                          type="text"
                          name="lastname"
                          value={this.state.lastname}
                          className="form-control"
                          onChange={this.handleInputChange}
                          maxLength={80}
                          placeholder="Enter your lastname"
                        />
                      </div>
                    </div>
                    {this.state.errors.lastname && (
                      <div className="imp textSize">
                        {this.state.errors.lastname}
                      </div>
                    )}

                    <div>
                      <label className="labels pt-4">
                        <span className="imp">*&nbsp;</span>Date of birth
                      </label>
                      <div className="input-group">
                        <input
                          type="date"
                          name="dob"
                          value={this.myDate(this.state.dob)}
                          className="form-control"
                          onChange={this.handleDate}
                          placeholder="Enter your dob"
                        />
                      </div>
                    </div>
                    {this.state.errors.dob && (
                      <div className="imp textSize">
                        {this.state.errors.dob}
                      </div>
                    )}

                    <div>
                      <label className="labels pt-4">
                        <span className="imp">*&nbsp;</span>Gender
                      </label>
                      <div className="input-group">
                        <select
                          name="gnder"
                          value={this.state.gnder}
                          className="form-control inputBasicInfo"
                          onChange={this.handleInputChange}
                        >
                          <option value="">Select gnder</option>
                          <option value="M">Male</option>
                          <option value="F">Female</option>
                        </select>
                      </div>
                    </div>
                    {this.state.errors.gnder ? (
                      <div className="imp textSize">
                        {this.state.errors.gnder}
                      </div>
                    ) : null}

                    <div>
                      <label className="labels pt-4">
                        <span className="imp">*&nbsp;</span>Select country
                      </label>
                      <div className="input-group">
                        <select
                          name="countryid"
                          value={this.state.countryid}
                          className="form-control inputBasicInfo"
                          onChange={this.handleInputChange}
                          onClick={() => this.getCities(this.state.countryid)}
                        >
                          <option value="">Select Country</option>
                          {this.props.getallcountries.map(country => (
                            <option
                              key={country.countryid}
                              value={country.countryid}
                            >
                              {country.countryname}
                            </option>
                          ))}
                        </select>
                      </div>
                    </div>
                    {this.state.errors.countryid ? (
                      <div className="imp textSize">
                        {this.state.errors.countryid}
                      </div>
                    ) : null}

                    <div>
                      <label className="labels pt-4">
                        <span className="imp">*&nbsp;</span>Select city
                      </label>
                      <div className="input-group">
                        <select
                          name="cityid"
                          value={!this.state.countryid ? "" : this.state.cityid}
                          className="form-control inputBasicInfo"
                          onChange={this.handleInputChange}
                          disabled={
                            isEmpty(this.state.countryid) ? true : false
                          }
                        >
                          <option value="">Select city</option>
                          {isEmpty(this.props.getallcitybycountry)
                            ? null
                            : this.props.getallcitybycountry.map(city => (
                                <option key={city.cityid} value={city.cityid}>
                                  {city.cityname}
                                </option>
                              ))}
                        </select>
                      </div>
                    </div>
                    {this.state.errors.cityid ? (
                      <div className="imp textSize">
                        {this.state.errors.cityid}
                      </div>
                    ) : null}

                    <div>
                      <label className="labels pt-4">
                        <span className="imp">*&nbsp;</span>Mailing Address
                      </label>
                      <textarea
                        type="text"
                        name="mailingaddress"
                        value={this.state.mailingaddress}
                        className="form-control"
                        onChange={this.handleInputChange}
                        placeholder="Enter you mailing address"
                      />
                    </div>
                    {this.state.errors.mailingaddress ? (
                      <div className="imp textSize">
                        {this.state.errors.mailingaddress}
                      </div>
                    ) : null}

                    <div>
                      <label className="labels pt-4">
                        <span className="imp">*&nbsp;</span>Select Qualification
                      </label>
                      <div className="input-group">
                        <select
                          name="qualificationid"
                          value={this.state.qualificationid}
                          className="form-control inputBasicInfo"
                          onChange={this.handleInputChange}
                        >
                          <option value="">Select Qualification</option>
                          {this.props.getqualification.map(qualification => (
                            <option
                              key={qualification.QulId}
                              value={qualification.QulId}
                            >
                              {qualification.QulDsc}
                            </option>
                          ))}
                        </select>
                      </div>
                    </div>
                    {this.state.errors.qualificationid ? (
                      <div className="imp textSize">
                        {this.state.errors.qualificationid}
                      </div>
                    ) : null}

                    <div>
                      <label className="labels pt-4">
                        <span className="imp">*&nbsp;</span>Password
                      </label>
                      <div className="input-group">
                        <input
                          type="text"
                          name="Pwd"
                          value={this.state.Pwd}
                          className="form-control"
                          onChange={this.handleInputChange}
                          maxLength={80}
                          placeholder="Enter your Password"
                        />
                      </div>
                    </div>
                    {this.state.errors.Pwd && (
                      <div className="imp textSize">
                        {this.state.errors.Pwd}
                      </div>
                    )}

                    <div>
                      <label className="labels pt-4">
                        <span className="imp">*&nbsp;</span>Email
                      </label>
                      <div className="input-group">
                        <input
                          type="text"
                          name="emailAddr"
                          value={this.state.emailAddr}
                          className="form-control"
                          onChange={this.handleInputChange}
                          maxLength={80}
                          placeholder="Enter your Email"
                        />
                      </div>
                    </div>
                    {this.state.errors.emailAddr && (
                      <div className="imp textSize">
                        {this.state.errors.emailAddr}
                      </div>
                    )}

                    <div>
                      <label className="labels pt-4">
                        <span className="imp">*&nbsp;</span>Company
                      </label>
                      <div className="input-group">
                        <select
                          disabled
                          name="CompId"
                          value={this.state.CompId}
                          className="form-control inputBasicInfo"
                          onChange={this.handleInputChange}
                        >
                          <option value="">Company</option>
                          {this.props.getallcompanies.map(company => (
                            <option key={company.CompId} value={company.CompId}>
                              {company.CompNm}
                            </option>
                          ))}
                        </select>
                      </div>
                    </div>
                    {this.state.errors.CompId && (
                      <div className="imp textSize">
                        {this.state.errors.CompId}
                      </div>
                    )}
                    <div>
                      <label className="labels pt-4">
                        <span className="imp">*&nbsp;</span>Select User Status
                      </label>
                      <div className="input-group">
                        <select
                          name="UserStatus"
                          value={this.state.UserStatus}
                          className="form-control inputBasicInfo"
                          onChange={this.handleInputChange}
                        >
                          <option value="">Select User Status</option>
                          <option value="A">Active</option>
                          <option value="I">Inactive</option>
                        </select>
                      </div>
                    </div>
                    {this.state.errors.UserStatus && (
                      <div className="imp textSize">
                        {this.state.errors.UserStatus}
                      </div>
                    )}
                    {/* Edit Modal Body End */}
                  </div>
                  <div className="modal-footer">
                    <button
                      type="button"
                      className="btn btn-secondary"
                      data-dismiss="modal"
                      onClick={() =>
                        this.setState({
                          userid: "",
                          firstname: "",
                          middlename: "",
                          lastname: "",
                          dob: "",
                          cnic: "",
                          gnder: "",
                          countryid: "",
                          cityid: "",
                          mailingaddress: "",
                          qualificationid: "",
                          Pwd: "",
                          emailAddr: "",
                          CompId: "",
                          FutureQualificationId: "",
                          UserStatus: "",
                          errors: {}
                        })
                      }
                    >
                      Close
                    </button>
                    <button
                      type="button"
                      className="btn btn-primary secondaryButton"
                      onClick={this.onClick}
                    >
                      Save changes
                    </button>
                  </div>
                </div>
              </div>
            </div>
            {/* Edit Modal End */}
            {/* Delete Modal Start */}
            <div
              className="modal fade modalHarami"
              id="deleteModal"
              tabIndex="-1"
              role="dialog"
              aria-labelledby="deleteModalLabel"
              aria-hidden="true"
            >
              <div
                className="modal-dialog modal-dialog-scrollable"
                role="document"
              >
                <div className="modal-content">
                  <div className="modal-header">
                    <h5 className="modal-title" id="deleteModalLabel">
                      Delete Course
                    </h5>
                    <button
                      type="button"
                      className="close"
                      data-dismiss="modal"
                      aria-label="Close"
                    >
                      <span aria-hidden="true">&times;</span>
                    </button>
                  </div>
                  <div className="modal-body">
                    {this.state.loadingDelete ? (
                      <div className="py-4 text-center">
                        <div className="spinner-border" role="status">
                          <span className="sr-only">Loading...</span>
                        </div>
                      </div>
                    ) : (
                      <div>Are you sure?</div>
                    )}
                  </div>
                  <div className="modal-footer">
                    <button
                      type="button"
                      className="btn btn-secondary"
                      data-dismiss="modal"
                      onClick={() => this.setState({ errors: {} })}
                    >
                      Close
                    </button>
                    <button
                      type="button"
                      className="btn btn-danger"
                      onClick={this.deleteUser}
                    >
                      Confirm
                    </button>
                  </div>
                </div>
              </div>
            </div>
            {/* Delete Modal End */}
          </div>
        )}
      </AdminDrawer>
    );
  }
}

const mapStateToProps = state => ({
  getallusersbycompany: state.admin.getallusersbycompany,
  updateuser: state.admin.updateuser,
  deleteuser: state.admin.deleteuser,
  getallcountries: state.root.getallcountries,
  getallcitybycountry: state.root.getallcitybycountry,
  getqualification: state.root.getqualification,
  getallcompanies: state.root.getallcompanies,
  islogin: state.root.islogin,
});
export default withRouter(
  connect(
    mapStateToProps,
    {
      deleteUser,
      getAllUsersByCompany,
      getAllCountries,
      getAllCityByCountry,
      getQualification,
      getAllCompanies,
      updateUser,
      isLogin
    }
  )(StudentsList)
);
