import React from "react";
import DrawerView from "./DrawerView";
import { withRouter } from "react-router-dom";
import "./Profile.css";
import isEmpty from "./validation/is-empty";
import { connect } from "react-redux";
import {
  getUser,
  getAllCountries,
  getAllCompanies,
  getQualification,
  getAllCityByCountry,
  isLogin
} from "../../Actions/authActions";
import Loader from "../../Loader";

class Profile extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      file: "",
      firstname: "",
      middlename: "",
      lastname: "",
      dob: "",
      cnic: "",
      gender: "",
      country: "",
      city: "",
      mailingaddress: "",
      highestqualification: "",
      email: "",
      company: "",
      errors: {}
    };
  }

  handleInputChange = event => {
    this.setState({ errors: {} });

    this.setState({
      [event.target.name]: event.target.value
    });
  };

  componentDidMount() {
    this.props.getUser();
    this.props.getAllCountries();
    this.props.getAllCompanies();
    this.props.getQualification();
    this.props.isLogin();

    if (!isEmpty(this.props.islogin)) {
      if (!this.props.islogin) {
        this.props.history.push('/signin')
      }
    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.getuser) {
      this.setState({
        firstname: nextProps.getuser.data[0].firstname,
        middlename: nextProps.getuser.data[0].middlename,
        lastname: nextProps.getuser.data[0].lastname,
        dob: nextProps.getuser.data[0].dob,
        cnic: nextProps.getuser.data[0].cnic,
        gender: nextProps.getuser.data[0].gnder,
        country: nextProps.getuser.data[0].countryid,
        city: nextProps.getuser.city,
        mailingaddress: nextProps.getuser.data[0].mailingaddress,
        highestqualification: nextProps.getuser.data[0].qualificationid,
        email: nextProps.getuser.data[0].emailAddr,
        company: nextProps.getuser.data[0].CompId
      });
    }
  }

  submitProfile = () => {
    const errors = {};

    if (isEmpty(this.state.firstname)) {
      errors.firstname = "First Name is Required";
    }

    if (isEmpty(this.state.middlename)) {
      errors.middlename = "Middle Name is Required";
    }

    if (isEmpty(this.state.lastname)) {
      errors.lastname = "Last Name is Required";
    }

    if (isEmpty(this.state.dob)) {
      errors.dob = "Date of Birth is Required";
    }

    if (isEmpty(this.state.cnic)) {
      errors.cnic = "CNIC is Required";
    }

    if (isEmpty(this.state.gender)) {
      errors.gender = "Gender is Required";
    }

    if (isEmpty(this.state.country)) {
      errors.country = "Country is Required";
    }

    if (isEmpty(this.state.city)) {
      errors.city = "City is Required";
    }

    if (isEmpty(this.state.mailingaddress)) {
      errors.mailingaddress = "Mailing Address is Required";
    }

    if (isEmpty(this.state.highestqualification)) {
      errors.highestqualification = "Qualification is Required";
    }

    if (
      /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(this.state.email)
      // /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(this.state.email)
    ) {
      errors.email = "Enter a valid email";
    }

    if (/^[0-9+]{5}-[0-9+]{7}-[0-9]{1}$/.test(this.state.cnic)) {
      errors.cnic = "Enter a valid CNIC";
    }

    if (!isEmpty(errors)) {
      this.setState({ errors });
    }
  };

  myDate = date => {
    let newDate;
    newDate = new Date(date);
    let day = newDate.getDate();
    // let month = monthNames[newDate.getMonth()];
    let year = newDate.getFullYear();
    return `${year}-${
      newDate.getMonth() >= 1 && newDate.getMonth() <= 9
        ? `0${newDate.getMonth()}`
        : newDate.getMonth()
      }-${day >= 1 && day <= 9 ? `0${day}` : day}`;
  };

  render() {
    return isEmpty(this.props.getuser) ||
      // isEmpty(this.state.firstname) ||
      // isEmpty(this.state.middlename) ||
      // isEmpty(this.state.lastname) ||
      // isEmpty(this.state.dob) ||
      // isEmpty(this.state.cnic) ||
      // isEmpty(this.state.gender) ||
      // isEmpty(this.state.country) ||
      isEmpty(this.state.city) ||
      // isEmpty(this.state.mailingaddress) ||
      // isEmpty(this.state.highestqualification) ||
      // isEmpty(this.state.email) ||
      // isEmpty(this.state.company) ||
      isEmpty(this.props.getallcountries) ||
      isEmpty(this.props.getqualification) ||
      isEmpty(this.props.getallcompanies) ? (
        <Loader />
      ) : (
        <DrawerView>
          <div>
            <h2 className="pl-5 pb-2">Sign Up</h2>
            <div className="container profileContainer mb-4">
              <div className="row">
                <div className="col-sm-12 col-md-3 text-center">
                  <img
                    src="https://via.placeholder.com/200"
                    alt="..."
                    className="img-thumbnail rounded"
                  />
                  <div className="custom-file customFile">
                    <input
                      type="file"
                      className="custom-file-input"
                      id="inputGroupFile01"
                      name="file"
                      onChange={this.handleInputChange}
                      value={this.state.file}
                    />
                    <label
                      className="custom-file-label customLabel"
                      htmlFor="inputGroupFile01"
                    >
                      Choose file
                  </label>
                  </div>
                </div>
                <div className="col-sm-12 col-md-9 nameRow">
                  <div className="row">
                    <div className="col-md-4 col-sm-12">
                      <label className="labels">
                        <span className="imp">*&nbsp;</span>First Name
                    </label>
                      <input
                        type="text"
                        name="firstname"
                        className="form-control formName"
                        onChange={this.handleInputChange}
                        value={this.state.firstname}
                      />
                      {this.state.errors.firstname ? (
                        <div className="imp textSize">
                          {this.state.errors.firstname}
                        </div>
                      ) : null}
                    </div>
                    <div className="col-md-4 col-sm-12">
                      <label className="labels">
                        <span className="imp">*&nbsp;</span>Middle Name
                    </label>
                      <input
                        type="text"
                        name="middlename"
                        className="form-control formName"
                        onChange={this.handleInputChange}
                        value={this.state.middlename}
                      />
                      {this.state.errors.middlename ? (
                        <div className="imp textSize">
                          {this.state.errors.middlename}
                        </div>
                      ) : null}
                    </div>
                    <div className="col-md-4 col-sm-12">
                      <label className="labels">
                        <span className="imp">*&nbsp;</span>Last Name
                    </label>
                      <input
                        type="text"
                        name="lastname"
                        className="form-control formName"
                        onChange={this.handleInputChange}
                        value={this.state.lastname}
                      />
                      {this.state.errors.lastname ? (
                        <div className="imp textSize">
                          {this.state.errors.lastname}
                        </div>
                      ) : null}
                    </div>
                  </div>
                  <br />
                  <div className="row">
                    <div className="col-md-12 col-sm-12">
                      <label className="labels">
                        <span className="imp">*&nbsp;</span>Date of birth
                    </label>
                      <input
                        type="date"
                        name="dob"
                        className="form-control formName"
                        onChange={this.handleInputChange}
                        value={this.myDate(this.state.dob)}
                        placeholder="Enter you date of birth"
                      />
                      {this.state.errors.dob ? (
                        <div className="imp textSize">
                          {this.state.errors.dob}
                        </div>
                      ) : null}
                    </div>
                  </div>
                </div>

                <div className="col-sm-12 col-md-10 nameRow cnic">
                  <div className="row">
                    <div className="col-md-8 col-sm-12">
                      <label className="labels">
                        <span className="imp">*&nbsp;</span>CNIC{" "}
                        <span className="notice">
                          Example: 33201-2222222-2 (With Dashes). In case you do
                          not have your own CNIC, please enter your
                          parents/guardian CNIC here
                      </span>
                      </label>
                      <input
                        type="text"
                        name="cnic"
                        className="form-control formName"
                        onChange={this.handleInputChange}
                        value={this.state.cnic}
                        placeholder="33201-2222222-2"
                      />
                      {this.state.errors.cnic ? (
                        <div className="imp textSize">
                          {this.state.errors.cnic}
                        </div>
                      ) : null}
                    </div>
                  </div>

                  <br />

                  <div className="row">
                    <div className="col-md-4 col-sm-12">
                      <label className="labels">
                        <span className="imp">*&nbsp;</span>Gender
                    </label>
                      <select
                        className="custom-select formName"
                        name="gender"
                        onChange={this.handleInputChange}
                        value={this.state.gender}
                      >
                        <option value="">
                          Select Gender
                      </option>
                        <option value="M">
                          Male
                      </option>
                        <option value="F">Female</option>
                      </select>
                      {this.state.errors.gender ? (
                        <div className="imp textSize">
                          {this.state.errors.gender}
                        </div>
                      ) : null}
                    </div>
                    <div className="col-md-4 col-sm-12">
                      <label className="labels">
                        <span className="imp">*&nbsp;</span>Country
                    </label>
                      <select
                        disabled
                        className="custom-select formName"
                        name="country"
                        onChange={this.handleInputChange}
                        value={this.state.country}
                        onClick={() => {
                          this.props.getAllCityByCountry(this.state.country)
                        }}
                      >
                        <option value="">Choose Country..</option>
                        {this.props.getallcountries.map(country => {
                          return (
                            <option
                              value={country.countryid}
                              key={country.countryid}
                            >
                              {country.countryname}
                            </option>
                          );
                        })}
                      </select>
                      {this.state.errors.country ? (
                        <div className="imp textSize">
                          {this.state.errors.country}
                        </div>
                      ) : null}
                    </div>
                    <div className="col-md-4 col-sm-12">
                      <label className="labels">
                        <span className="imp">*&nbsp;</span>City
                    </label>
                      <select
                        disabled
                        className="custom-select"
                        name="city"
                        onChange={this.handleInputChange}
                        value={this.state.city ? this.state.city[0].cityid : ""}
                      >
                        <option value="">Choose City..</option>
                        {this.props.getallcitybycountry
                          ? this.props.getallcitybycountry.map(city => {
                            return (
                              <option value={city.cityid} key={city.cityid}>
                                {city.cityname}
                              </option>
                            );
                          })
                          : this.state.city.map(city => {
                            return (
                              <option value={city.cityid} key={city.cityid}>
                                {city.cityname}
                              </option>
                            );
                          })}
                      </select>
                      {this.state.errors.city ? (
                        <div className="imp textSize">
                          {this.state.errors.city}
                        </div>
                      ) : null}
                    </div>
                  </div>
                  <br />
                  <div className="row">
                    <div className="col-12">
                      <label className="labels">
                        <span className="imp">*&nbsp;</span>Mailing Address
                    </label>
                      <textarea
                        className="form-control"
                        name="mailingaddress"
                        aria-label="With textarea"
                        onChange={this.handleInputChange}
                        value={this.state.mailingaddress}
                      />
                      {this.state.errors.mailingaddress ? (
                        <div className="imp textSize">
                          {this.state.errors.mailingaddress}
                        </div>
                      ) : null}
                    </div>
                  </div>
                  <br />
                  <div className="row">
                    <div className="col-sm-12 col-md-12">
                      <label className="labels">
                        <span className="imp">*&nbsp;</span>Email
                    </label>
                      <div className="pl-2 notice">
                        Example: abc@hotmail.com. Please enter valid email
                        address, so that we are be able to contact with you via
                        email.
                    </div>
                      <input
                        className="form-control"
                        name="email"
                        onChange={this.handleInputChange}
                        value={this.state.email}
                      />
                      {this.state.errors.email ? (
                        <div className="imp textSize">
                          {this.state.errors.email}
                        </div>
                      ) : null}
                    </div>
                  </div>
                  <br />
                  <div className="row">
                    <div className="col-12">
                      <label className="labels">
                        <span className="imp">*&nbsp;</span>Highest Qualification
                    </label>
                      <select
                        className="custom-select formName"
                        name="highestqualification"
                        onChange={this.handleInputChange}
                        value={this.state.highestqualification}
                      >
                        <option value="">Select Qualification..</option>
                        {this.props.getqualification.map(qualification => {
                          return (
                            <option
                              value={qualification.QulId}
                              key={qualification.QulId}
                            >
                              {qualification.QulDsc}
                            </option>
                          );
                        })}
                      </select>
                      {this.state.errors.highestqualification ? (
                        <div className="imp textSize">
                          {this.state.errors.highestqualification}
                        </div>
                      ) : null}
                    </div>
                  </div>
                  <br />
                  <button
                    type="button"
                    className="btn btn-success"
                    onClick={this.submitProfile}
                  >
                    Submit
                </button>
                </div>
              </div>
            </div>
          </div>
        </DrawerView>
      );
  }
}

const mapStateToProps = state => ({
  getuser: state.root.getuser,
  getallcountries: state.root.getallcountries,
  getallcompanies: state.root.getallcompanies,
  getqualification: state.root.getqualification,
  getallcitybycountry: state.root.getallcitybycountry,
  isloginuser: state.root.isloginuser,
});
export default withRouter(
  connect(
    mapStateToProps,
    {
      getUser,
      getAllCountries,
      getAllCompanies,
      getQualification,
      getAllCityByCountry,
      isLogin
    }
  )(Profile)
);
