import { secretkey } from "../../url"
import CryptoJS from "crypto-js";
const TOKEN_KEY = "atkn";

export const logout = history => {
  localStorage.removeItem(TOKEN_KEY);
  localStorage.removeItem("acid");
  localStorage.removeItem("auid");
  history.push("/admin");
};

export const isLogin = () => {
  if (localStorage.getItem(TOKEN_KEY)) {
    return true;
  }
  return false;
};

export const decryptId = () => {
  let compId = localStorage.getItem("acid");
  if (compId) {
  if (compId.toString()) {
    var bytes = CryptoJS.AES.decrypt(
        compId.toString(),
        secretkey
    );
    compId = bytes.toString(CryptoJS.enc.Utf8);
    return compId
  }
  }
  return null
};

export const decryptUserId = () => {
  let userid = localStorage.getItem("auid");
  if (userid) {
  if (userid.toString()) {
    var bytes = CryptoJS.AES.decrypt(
      userid.toString(),
      secretkey
    );
    userid = bytes.toString(CryptoJS.enc.Utf8);
    return userid
  }
  }

  return null
};

export const decryptToken = () => {
  let token = localStorage.getItem("atkn");
  if (token) {
  if (token.toString()) {
    var bytes = CryptoJS.AES.decrypt(
      token.toString(),
      secretkey
    );
    token = bytes.toString(CryptoJS.enc.Utf8);
    return token
  }
  }
  return null
};