export const getUser = () => dispatch => {
  axios
    .get(`${url}GetUser?Userid=${decryptUserId()}`)
    .then(res => {
      axios
        .get(`${url}GetAllCityByCountry?CountryId=${res.data[0].countryid}`)
        .then(resFinal => {
          dispatch({
            type: "GET_USER",
            payload: {
              data: res.data,
              city: resFinal.data
            }
          });
        })
        .catch(err => console.log(err));
    })
    .catch(err =>
      dispatch({
        type: "GET_USER",
        payload: 500
      })
    );
};
