import React from "react";
import AdminDrawer from "./AdminDrawer";
import { withRouter } from "react-router-dom";
import { getInstructorById, isLogin } from "../../Actions/adminActions";
import { connect } from "react-redux";
import "./AddCourse.css";
// import EditorDesc from "./Editor";
import Loader from "../../Loader";
import isEmpty from "../Protected/validation/is-empty";
import "./Instructor.css";
class Instructor extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      courseImage: null,
      courseImageURL: null,
      name: "",
      desc: "",
      errors: {},
      loading: false
    };
  }

  componentDidMount() {
    this.props.getInstructorById(this.props.match.params.name);

    this.props.isLogin();

    if(!isEmpty(this.props.islogin)){
      if(!this.props.islogin){
        this.props.history.push('/admin')
      }
    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.getinstructorbyid) {
      this.setState({
        name: nextProps.getinstructorbyid[0].instructorName,
        desc: nextProps.getinstructorbyid[0].Description,
        courseImage: nextProps.getinstructorbyid[0].ImgUrl
      });
    }
  }

  handleImage = event => {
    if (event.target.files && event.target.files[0]) {
      this.setState({
        courseImageURL: URL.createObjectURL(event.target.files[0])
      });
    }
    // this.setState({
    //   courseImage: event.target.files[0]
    // });
  };

  handleInputChange = event => {
    this.setState({ errors: {} });

    this.setState({
      [event.target.name]: event.target.value
    });
  };

  onClick = e => {
    e.preventDefault();
    const errors = {};

    if (isEmpty(this.state.name)) {
      errors.name = "Name is Required";
    }

    if (isEmpty(this.state.desc)) {
      errors.desc = "Description is Required";
    }

    this.setState({ errors });
    if (isEmpty(errors)) {
      this.setState({ loading: true });
      // this.props.addCourseMain(values, this.props.history);
    }
  };

  render() {
    return isEmpty(this.props.getinstructorbyid) ? (
      <Loader />
    ) : (
        <AdminDrawer>
          <nav className="container containerAddCourse">
            <div className="nav nav-tabs" id="nav-tab" role="tablist">
              <a
                className="nav-item nav-link active"
                id="nav-home-tab"
                data-toggle="tab"
                href="#nav-home"
                role="tab"
                aria-controls="nav-home"
                aria-selected="true"
              >
                <strong>Instructor</strong>
              </a>
            </div>
          </nav>
          <div
            className="tab-content containerAddCourse profileContainer"
            id="nav-tabContent"
          >
            <div
              className="tab-pane fade show active"
              id="nav-home"
              role="tabpanel"
              aria-labelledby="nav-home-tab"
            >
              <div className="container pt-4">
                <img
                  src={this.state.courseImageURL ? this.state.courseImageURL : `http://198.37.118.15:7899/${this.state.courseImage}`}
                  alt="..."
                  className="img-thumbnail rounded mx-auto d-block centerBastard"
                />
                <br />
                <div className="custom-file">
                  <input
                    type="file"
                    className="custom-file-input"
                    id="inputGroupFile01"
                    name="file"
                    onChange={this.handleImage}
                  />
                  <label
                    className="custom-file-label customLabel"
                    htmlFor="inputGroupFile01"
                  >
                    Choose image
                </label>
                </div>

                <div>
                  <label className="labels pt-4">
                    <span className="imp">*&nbsp;</span>Instructor Name
                </label>
                  <div className="input-group">
                    <input
                      type="text"
                      name="name"
                      value={this.state.name}
                      className="form-control"
                      onChange={this.handleInputChange}
                      maxLength={80}
                      placeholder="Name"
                    />
                    <div className="input-group-append">
                      <span className="input-group-text">
                        {this.state.name.length < 80
                          ? 80 - this.state.name.length
                          : 0}
                      </span>
                    </div>
                  </div>
                </div>
                {this.state.errors.name && (
                  <div className="imp textSize">{this.state.errors.name}</div>
                )}

                <div>
                  <label className="labels pt-4">
                    <span className="imp">*&nbsp;</span>Description
                </label>
                  <div className="input-group">
                    <textarea
                      name="desc"
                      value={this.state.desc}
                      className="form-control"
                      onChange={this.handleInputChange}
                      placeholder="Description"
                    />
                  </div>
                </div>
                {this.state.errors.desc && (
                  <div className="imp textSize">{this.state.errors.desc}</div>
                )}
                <br />
                <div className="container">
                  <div className="row">
                    <div className="col-sm-12 col-md-6 buttonClassLeft">
                      <button className="btn btn-success btn-lg mb-2" onClick={this.onClick}>Update</button>
                    </div>
                    <div className="col-sm-12 col-md-6 buttonClassRight">
                      <button className="btn btn-danger btn-lg" onClick={this.onClick}>Delete&nbsp;</button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </AdminDrawer>
      );
  }
}

const mapStateToProps = state => ({
  getinstructorbyid: state.admin.getinstructorbyid,
  islogin: state.admin.islogin,
});
export default withRouter(
  connect(
    mapStateToProps,
    { getInstructorById, isLogin }
  )(Instructor)
);
