import React,{ useState, useEffect } from "react";
import { Route, Redirect } from "react-router-dom";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { isLogin } from "../Actions/adminActions";
import isEmpty from "../Components/Protected/validation/is-empty";
import Loader from "../Loader";
const PrivateRoute = ({ component: Component, userType, isLogin, islogin, ...rest }) => {

  useEffect(() => {
    isLogin();
  }, []);
  console.log(islogin)
  return (
    // Show the component only when the user is logged in
    // Otherwise, redirect the user to /signin page
    isEmpty(islogin) ? 
    <Loader/> :
    <Route
      {...rest}
      render={props =>
        islogin ? userType === "admin" ? <Component {...props} /> : <Redirect to="/404" /> : <Redirect to="/admin" />
      }
    />
  );
};

const mapStateToProps = state => ({
  islogin: state.admin.islogin,
});
export default withRouter(
  connect(
    mapStateToProps,
    { isLogin }
  )(PrivateRoute)
);
