import React,{ useState, useEffect } from "react";
import { Route, Redirect } from "react-router-dom";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { isLogin } from "../Actions/authActions";
import isEmpty from "../Components/Protected/validation/is-empty";
import Loader from "../Loader";
const PrivateRoute = ({ component: Component, userType, isLogin, isloginuser, ...rest }) => {

  useEffect(() => {
    isLogin();
  }, []);
  console.log(isloginuser)
  return (
    // Show the component only when the user is logged in
    // Otherwise, redirect the user to /signin page
    isEmpty(isloginuser) ? 
    <Loader/> :
    <Route
      {...rest}
      render={props =>
        isloginuser ? userType === "student" ? <Component {...props} /> : <Redirect to="/404" /> : <Redirect to="/signin" />
      }
    />
  );
};

const mapStateToProps = state => ({
  isloginuser: state.root.isloginuser,
});
export default withRouter(
  connect(
    mapStateToProps,
    { isLogin }
  )(PrivateRoute)
);
